body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Example-container {
    border: 1px solid #dadde0;
    max-width: 320px;
}
.RddItem-container {
    position: relative;
    -webkit-transition: height 600ms cubic-bezier(0.7, 0, 0.3, 1);
    transition: height 600ms cubic-bezier(0.7, 0, 0.3, 1);
}
.RddItem-itemsList {
    list-style-type: none;
    padding: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    margin: 0;
}
.RddItem-itemsList li {
    -webkit-animation-duration: 600ms;
            animation-duration: 600ms;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
            animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
    margin: 0;
}
.RddItem-animateOutToRight {
    pointer-events: none;
}
.RddItem-animateOutToRight li {
    -webkit-animation-name: outToRight;
            animation-name: outToRight;
}
.RddItem-animateOutToLeft {
    pointer-events: none;
}
.RddItem-animateOutToLeft li {
    -webkit-animation-name: outToLeft;
            animation-name: outToLeft;
}
.RddItem-animateInFromLeft li {
    -webkit-animation-name: inFromLeft;
            animation-name: inFromLeft;
}
.RddItem-animateInFromRight li {
    -webkit-animation-name: inFromRight;
            animation-name: inFromRight;
}
.RddItem-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px;
}
.RddItem-item:hover {
    text-decoration: none;
    background-color: #f2f2f2;
}
.RddItem-button {
    background-color: #9ec0d7;
    cursor: pointer;
    color: #64422b;
    position: relative;
    width: 100%;
    border: none;
    font-size: 15px;
    line-height: 20px;
}
.RddItem-button:after {
    color: #64422b;
    content: '\2794';
    position: absolute;
    right: 12px;
    -webkit-transition: right 150ms ease;
    transition: right 150ms ease;
}
.RddItem-button:hover:after {
    color: #003336;
    right: 10px;
}

.Rdd-Header-container {
    font-size: 13px;
    background-color: #9ec0d7;
    border-bottom: lightgrey;
    padding: 10px;
    color: #64422b;
}

.Rdd-Header-backButton {
    color: #64422b;
    margin-right: 10px;
    font-size: 13px;
    background: none;
    border: 0;
    padding: 0;
}

.Rdd-Header-backButton:before {
    content: '\2190   ';
}

@-webkit-keyframes outToRight {
    to {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
}
@keyframes outToRight {
    to {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
}
@-webkit-keyframes outToLeft {
    to {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
}
@keyframes outToLeft {
    to {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
}
@-webkit-keyframes inFromLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@keyframes inFromLeft {
    from {
        opacity: 0;
        -webkit-transform: translate3d(-80%, 0, 0);
        transform: translate3d(-80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@-webkit-keyframes inFromRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@keyframes inFromRight {
    from {
        opacity: 0;
        -webkit-transform: translate3d(80%, 0, 0);
        transform: translate3d(80%, 0, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

